                <h4>Voto Anticipado</h4>
              </div>

              <div v-if="eleccion =='Diputaciones' ">                  
                <div v-if="listaDistritos !='7' && listaDistritos !='8' && listaDistritos !='9' && listaDistritos !='10' && listaDistritos !='13'" >
