          font-size: 1.5rem !important;
          font-weight: bold !important;
        }
        .titulo{
          font-size: 1rem !important;
          font-weight: bold !important;
        }
        svg{
            background-color: black;
            color: white;
            padding: 5px;
            border-radius: 50px;
            width: 20px;
            font-size: 20px;
        }
      }
    }
  }
}

.boton-verdetalle {
  background-color: #582D73;
  border-color: transparent;
}

